import React, { useContext, useState } from 'react'
import Layout from '../../../app/AppLayout'
import { AppContext } from '../../../context/app-context'

import { Button } from '@material-ui/core'
import GetAppIcon from '@material-ui/icons/GetApp'
import FormatIndentIncreaseIcon from '@material-ui/icons/FormatIndentIncrease'

import {
  useTheme,
  createStyles,
  makeStyles,
  Theme
} from '@material-ui/core/styles'

const useStyles = makeStyles((theme: Theme) =>
  createStyles({
    content: {
      width: '100%', // Fix IE 11 issue.
      marginTop: theme.spacing(1)
    },
    button: {
      margin: theme.spacing(2)
    }
  })
)

interface FormState {
  loading: boolean
  generated: boolean
}

export interface AuthProps {}

const Auth: React.SFC<AuthProps> = () => {
  const classes = useStyles(useTheme())
  const [state, setState] = useState<FormState>({
    loading: false,
    generated: false
  })

  const appContext = useContext(AppContext)

  const submitHandler = (e: any) => {
    e.preventDefault()
    console.log('submit', e)
  }

  const data = {
    id: '2011201908482221234',
    organization: 'My Organization',
    partner: {
      firstName: 'Sabrina',
      middleName: '',
      lastName: 'Nabor',
      phone: '789 7898 78979',
      ext: '7260',
      email: 'sonabstudios@gmail.com',
      oralEnglish: true,
      oralFrench: true,
      writtenEnglish: true,
      writtenFrench: true
    },
    alternative: {
      firstName: 'Chris',
      middleName: 'Castro',
      lastName: 'Holley',
      phone: '+1 903 372 9902',
      ext: '000',
      email: 'ChrisEHolley@rhyta.com',
      oralEnglish: true,
      oralFrench: false,
      writtenEnglish: true,
      writtenFrench: false
    }
  }

  const handleFillClick = () => {
    if (!state.loading) {
      appContext.postFillForm(data)
    }
  }

  return (
    <Layout title='PDF Forms Filler'>
      <Button
        className={classes.button}
        color='primary'
        variant='outlined'
        disabled>
        Save
      </Button>
      <Button
        className={classes.button}
        color='primary'
        variant='outlined'
        endIcon={<GetAppIcon />}
        disabled>
        Download
      </Button>
      <Button
        className={classes.button}
        variant='outlined'
        component='span'
        color='primary'
        disabled={state.loading}
        endIcon={<FormatIndentIncreaseIcon />}
        onClick={() => handleFillClick()}>
        FILL TO FORM
      </Button>
    </Layout>
  )
}

export default Auth
